.Contactme{
    width: 100%;
    background-color: #10172a;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    color: antiquewhite;
}

form{
    display: flex;
    flex-direction: column;
    top: 30%;
    left: 50%;
    width: 50vw;
    gap: 10px;
    align-items: center;
}

input , textarea{
    padding: 20px;
    text-align: left;
    border-radius: 5px;
    border: none;
    width: 100%;
    box-shadow:  inset -3px -3px 7px #ffffffb2,
        inset 1px 1px 2px rgba(94, 104, 121, 0.945);
}
textarea{
    height: 20vh;
}

.Submit{
    padding: 10px;
    width: 30%;
    border: none;
    border-radius: 5px;
}
.Submit:hover{
    box-shadow:  inset -3px -3px 7px #ffffffb2,
        inset 1px 1px 2px rgba(94, 104, 121, 0.945);
}

label{
    text-align: left;
    width: 100%;
    margin-top: 10px;
    font-size: 1.2em;
}

footer{
    display: flex;
    justify-content: right;
    padding: 30px 20px;
    background-color: #10172a;
    color: antiquewhite;
}