.codepenData{
    height: 458px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1em;
    border-radius: 10px;
}     
.codeTitles{
    border-bottom: 1px #000 solid;
    width: 100%;
    padding-bottom: 10px;
}
.messages{
    width: 100%;
    margin-bottom: 30px;
    text-align: left;
}
.codeBoxes{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    width: 50vw;
    padding: 20px;
    margin-bottom: 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}