.card{
    position: absolute;
    width: 20.8vw;
    height: 30.3vw;
    border-radius: 10px;
    transition: 0.5s;
    transform-style: preserve-3d;
    margin: 0 !important;
    transform: translate(-50%,-50%);
    z-index: 1;
}
#monstercard{
    top: 18%;
    left: 25%;
}
.mypic{
    position: absolute;
    top: 5vw;
    left: 2.4vw;
    z-index: -1;
}
.mypic img{
    width: 18vw;
}
.front{
    position: absolute;
    top: 0vh;
    left: 0vw;
    width: 20.8vw;
    height: 30.3vw;
    border-radius: 10px;
    z-index: 0;
    overflow: hidden;
    visibility: visible;
    -webkit-backface-visibility: hidden;
}
.cardFront{
    width: 20.8vw;
    height: 30.3vw;
}
#back{
    position: absolute;
    top: 0;
    left: 0;
    width: 400px;
    height: 580px;
    border-radius: 8px;
    z-index: -2;
    -webkit-backface-visibility: hidden;
    transform: rotateY(180deg);
}
#cardTitle{
    font-family: 'Didact Gothic', sans-serif;
    font-size: 1.5vw;
    position: absolute;
    left: 1.8vw;
    top: .7vw;
    z-index: 5;
}
