#TopHolder{
    position: absolute;
    top: 0;
}

.navigator{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #10172a;
    z-index: 2;
    transition: 1000ms;
}

#Myname{
    display: none;
    position: absolute;
    left: 7vw;
    color: antiquewhite;
    font-size: 1.5em;
}

.pageTitle{
    margin-left: 60px;
    height: 60px;
    padding: 10px;
    margin-top: 10px;
}

.links{
    display: flex;
    width: 500px;
    display: flex;
    justify-content: space-evenly;
    margin-right: 20px;
    color: antiquewhite;
}

.testing{
    padding: 10px 30px;
    text-decoration: none;
    font-style: normal;
    transition: 500ms;
    border-radius: 10px;
    color: antiquewhite;
}